import pretty from 'pretty'
import { CSSProperties } from 'react'

export function makeId(length = 10) {
	let result = ''
	const characters = 'abcdefghijklmnopqrstuvwxyz0123456789'
	const charactersLength = characters.length
	let counter = 0
	while (counter < length) {
		if (counter === 0) result += characters.charAt(Math.floor(Math.random() * 26))
		else result += characters.charAt(Math.floor(Math.random() * charactersLength))
		counter += 1
	}
	return result
}

export const getGeneratedBlobHtml = (html: string, head: string, css?: string) => {
	const source = pretty(
		`
	<html lang="ja">
		<head>
			${head}
			<style>
				${css || ''}
			</style>
		</head>
		<body>
			${html}
		</body>
	</html>
`,
		{ ocd: true }
	)

	return source
}

export const setStyleCssToElm = (elm: HTMLElement, style: CSSProperties) => {
	return Object.keys(style).forEach((key) => {
		const k = key
			.split('')
			.map((s) => {
				if (s === s.toUpperCase()) {
					return '-' + s.toLowerCase()
				}
				return s
			})
			.join('')
		elm.style[k] = style[key]
	})
}
/**
 *
 * @param ms millisecond time ex: 1000*60
 * @returns Promise<void>
 */
export const delay = (ms: number) => new Promise<void>((res) => setTimeout(res, ms))

export const getBodyObj = async () => {
	const tmp = document.createElement('div')
	tmp.innerHTML = document.getElementById('edit-area')?.innerHTML || ''
	const editable = tmp.querySelectorAll<HTMLElement>('[data-editable]')

	if (!editable) return
	const bodyObj: {
		[key: string]: string
	} = {}
	await delay(0)

	editable.forEach((elm) => {
		const tmp = elm
		const key = elm.dataset.editable!
		tmp.querySelectorAll('[data-block="left-actions"],[data-block="bottom-actions"]').forEach((elm) => {
			elm.remove()
		})
		bodyObj[key] = tmp.outerHTML.replaceAll(/ contenteditable="true"/g, '')
	})
	return bodyObj
}
