import { css } from '@codemirror/lang-css'
import { html } from '@codemirror/lang-html'
import { javascript } from '@codemirror/lang-javascript'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import SaveIcon from '@mui/icons-material/Save'
import { LoadingButton } from '@mui/lab'
import { Accordion, AccordionDetails, AccordionSummary, Box, Button, Stack, Typography } from '@mui/material'
import { color } from '@uiw/codemirror-extensions-color'
import { monokai } from '@uiw/codemirror-theme-monokai'
import CodeMirror, { ReactCodeMirrorRef } from '@uiw/react-codemirror'
import { ThemeApi } from 'API/CMS/theme'
import Updating from 'Updating'
import EditInput from 'components/EditInput'
import { PERMISSION, TokenInfo } from 'interfaces'
import pretty from 'pretty'
import { useEffect, useRef, useState } from 'react'
import { AiOutlineDesktop, AiOutlineMobile } from 'react-icons/ai'
import { BiSolidComponent } from 'react-icons/bi'
import { GiStarFormation } from 'react-icons/gi'
import { PiSquareSplitHorizontal, PiSquareSplitVerticalLight } from 'react-icons/pi'
import { useLocation, useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getThemes } from 'redux/slices/CMS/Theme'
import { UIAction } from 'redux/slices/UI'
import { makeId } from 'utils/common'

export default function Update() {
	const { id } = useParams<{ id: string }>()
	const nav = useNavigate()
	const location = useLocation()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const dispatch = useAppDispatch()
	const status = useAppSelector((s) => s.CMS.Theme.status)
	const theme = useAppSelector((s) => s.CMS.Theme.list).find((f) => f.id === Number(id))
	const [loading, setLoading] = useState(false)
	const currentPath = useAppSelector((s) => s.UI.currentPath)
	const [updating, setUpdating] = useState<TokenInfo>()
	useEffect(() => {
		const currentEditor = currentPath.find((f) => f.path === location.pathname)
		if (currentEditor && currentEditor.wsId !== (window as any).wss.id) {
			setUpdating(currentEditor.auth)
		} else {
			setUpdating(undefined)
		}
	}, [currentPath, location.pathname])
	const [head, setHead] = useState(
		pretty(
			`
	<meta charset="utf-8" /><meta name="viewport" content="width=device-width, initial-scale=1" />`,
			{ ocd: true }
		)
	)
	const [body, setBody] = useState(pretty(``, { ocd: true }))
	const [style, setStyle] = useState('')
	const [script, setScript] = useState('')
	useEffect(() => {
		if (theme) {
			theme.head && setHead(pretty(theme.head, { ocd: true }))
			theme.body && setBody(pretty(theme.body, { ocd: true }))
			theme.style && setStyle(pretty(theme.style, { ocd: true }))
			theme.script && setScript(pretty(theme.script, { ocd: true }))
		}
	}, [theme])
	useEffect(() => {
		if (status === 'init') dispatch(getThemes())
		else if (id && !theme) nav('/cms/theme')
	}, [dispatch, id, nav, status, theme])

	const editorRef = useRef<ReactCodeMirrorRef>(null)
	const [viewWith, setViewWith] = useState<string>('100%')
	const [direction, setDirection] = useState<'row' | 'column'>('column')

	if (updating) return <Updating user={updating} />

	if (!id || !theme || !tokenInfo) return null

	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					color="primary.dark"
					sx={{
						fontWeight: 600,
					}}
				>
					テーマ設定
				</Typography>
				<Stack direction="row" gap={1} alignItems="center">
					<Typography>レイアウト切替</Typography>
					<Button
						variant={direction === 'row' ? 'outlined' : 'contained'}
						color="primary"
						onClick={() => {
							setDirection('column')
						}}
						sx={{
							minWidth: 0,
							p: 0.2,
							width: 32,
							height: 32,
							fontSize: 24,
						}}
					>
						<PiSquareSplitVerticalLight />
					</Button>
					<Button
						variant={direction === 'column' ? 'outlined' : 'contained'}
						color="primary"
						onClick={() => {
							setDirection('row')
						}}
						sx={{
							minWidth: 0,
							p: 0.2,
							width: 32,
							height: 32,
							fontSize: 24,
						}}
					>
						<PiSquareSplitHorizontal />
					</Button>
				</Stack>
			</Stack>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2}>
				<EditInput
					label="テーマ名"
					value={theme.name}
					apiEndpoint={'/api/cms/theme/' + id}
					fieldName="name"
					readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
				/>
				<LoadingButton
					sx={{
						display: 'flex',
						width: 160,
					}}
					loading={loading}
					loadingPosition="start"
					startIcon={<SaveIcon />}
					variant="outlined"
					onClick={async () => {
						try {
							setLoading(true)
							if (!body.match(/{block:(\d|\w){13}}/))
								return dispatch(
									UIAction.addGlobalMessage({
										message: 'Body: UI編集所まだ入ってないので、ご確認ください',
										type: 'warning',
									})
								)
							const { error, message } = await ThemeApi.update({
								id,
								head,
								body,
								script,
								style,
							})
							dispatch(
								UIAction.addGlobalMessage({
									message,
									type: error ? 'error' : 'success',
								})
							)
						} finally {
							setLoading(false)
						}
					}}
				>
					保存する
				</LoadingButton>
			</Stack>
			<Stack alignItems="stretch" gap={1} direction={direction}>
				<Stack
					gap={1}
					maxHeight={direction === 'row' ? '750px' : 'none'}
					className="scroll-bar"
					flex={direction === 'row' ? 2 : 1}
					sx={{
						outline: '1px solid #eee',
						p: '2px',
					}}
				>
					<Accordion>
						<AccordionSummary expandIcon={<ExpandMoreIcon />}>
							<Typography>Head</Typography>
						</AccordionSummary>
						<AccordionDetails>
							<Box
								bgcolor="#272822"
								sx={{
									border: '1px solid #ccc',
								}}
							>
								<Stack direction="row" p={0.5} gap={2} alignItems="center" bgcolor="#000">
									<Button
										variant="contained"
										aria-label="format"
										color="success"
										onClick={() => setHead(pretty(head, { ocd: true }))}
										sx={{
											minWidth: 0,
											p: 0.2,
											width: 36,
											height: 36,
											fontSize: 24,
										}}
									>
										<GiStarFormation />
									</Button>
								</Stack>
								<CodeMirror
									value={head}
									theme={monokai}
									autoFocus
									className="scroll-bar"
									minHeight="100px"
									maxHeight="700px"
									extensions={[html()]}
									onChange={(h) => {
										setHead(h)
									}}
									placeholder="Input html head-block code!"
								/>
							</Box>
						</AccordionDetails>
					</Accordion>
					<Accordion>
						<AccordionSummary expandIcon={<ExpandMoreIcon />}>
							<Typography>Style</Typography>
						</AccordionSummary>
						<AccordionDetails>
							<Box
								bgcolor="#272822"
								sx={{
									border: '1px solid #ccc',
								}}
							>
								<Stack direction="row" p={0.5} gap={2} alignItems="center" bgcolor="#000">
									<Button
										variant="contained"
										aria-label="format"
										color="success"
										onClick={() => setStyle(pretty(style, { ocd: true }))}
										sx={{
											minWidth: 0,
											p: 0.2,
											width: 36,
											height: 36,
											fontSize: 24,
										}}
									>
										<GiStarFormation />
									</Button>
								</Stack>
								<CodeMirror
									value={style}
									theme={monokai}
									autoFocus
									className="scroll-bar"
									minHeight="100px"
									maxHeight="700px"
									extensions={[css(), color]}
									onChange={(css) => {
										setStyle(css)
									}}
									placeholder="Input css code!"
								/>
							</Box>
						</AccordionDetails>
					</Accordion>
					<Accordion>
						<AccordionSummary expandIcon={<ExpandMoreIcon />}>
							<Typography>Body</Typography>
						</AccordionSummary>
						<AccordionDetails>
							<Box
								bgcolor="#272822"
								sx={{
									border: '1px solid #ccc',
								}}
							>
								<Stack direction="row" p={0.5} gap={2} alignItems="center" bgcolor="#000">
									<Button
										variant="contained"
										aria-label="format"
										color="success"
										id="body-code-format-btn"
										onClick={() => setBody(pretty(body, { ocd: true }))}
										sx={{
											minWidth: 0,
											p: 0.2,
											width: 36,
											height: 36,
											fontSize: 24,
										}}
									>
										<GiStarFormation />
									</Button>
									<Button
										sx={{
											py: 0.2,
											height: 36,
											fontSize: 24,
										}}
										variant="contained"
										color="warning"
										onClick={() => {
											const text = `\n{block:${makeId(13)}}\n`
											if (editorRef.current) {
												const editor = editorRef.current.editor
												const state = editorRef.current.view?.state
												const view = editorRef.current.view
												if (!editor || !state || !view) return
												view.dispatch(view.state.replaceSelection(text))
											}
										}}
									>
										<BiSolidComponent />
										<Typography variant="button">UI編集追加</Typography>
									</Button>
								</Stack>
								<CodeMirror
									ref={editorRef}
									value={body}
									theme={monokai}
									id="body-code"
									className="scroll-bar"
									minHeight="100px"
									maxHeight="700px"
									extensions={[html()]}
									onChange={(b) => {
										setBody(b)
									}}
									placeholder="Input html body-block code!"
								/>
							</Box>
						</AccordionDetails>
					</Accordion>
					<Accordion>
						<AccordionSummary expandIcon={<ExpandMoreIcon />}>
							<Typography>Script</Typography>
						</AccordionSummary>
						<AccordionDetails>
							<Box
								bgcolor="#272822"
								sx={{
									border: '1px solid #ccc',
								}}
							>
								<Stack direction="row" p={0.5} gap={2} alignItems="center" bgcolor="#000">
									<Button
										variant="contained"
										aria-label="format"
										color="success"
										onClick={() => setScript(pretty(script, { ocd: true }))}
										sx={{
											minWidth: 0,
											p: 0.2,
											width: 36,
											height: 36,
											fontSize: 24,
										}}
									>
										<GiStarFormation />
									</Button>
								</Stack>
								<CodeMirror
									value={script}
									theme={monokai}
									autoFocus
									className="scroll-bar"
									minHeight="100px"
									maxHeight="700px"
									extensions={[javascript()]}
									onChange={(sc) => {
										setScript(sc)
									}}
									placeholder="Input Javascript code!"
								/>
							</Box>
						</AccordionDetails>
					</Accordion>
				</Stack>
				<Stack
					flex={1}
					justifyContent="center"
					alignItems="center"
					overflow="auto"
					bgcolor="#3ac2"
					p={0.5}
					sx={{
						border: '1px solid #ccc',
						minWidth: 444,
					}}
				>
					<Stack
						flex={1}
						justifyContent="center"
						alignItems="center"
						sx={{
							width: viewWith,
							minWidth: 438,
							transition: 'all 500ms',
						}}
					>
						<Stack direction="row" p={0.5} gap={1} sx={{}}>
							<Button
								variant={parseInt(`${viewWith}`) <= 50 ? 'outlined' : 'contained'}
								color="warning"
								onClick={() => {
									setViewWith('100%')
								}}
								sx={{
									minWidth: 0,
									p: 0.2,
									width: 32,
									height: 32,
									fontSize: 24,
								}}
							>
								<AiOutlineDesktop />
							</Button>
							<Button
								variant={parseInt(`${viewWith}`) >= 50 ? 'outlined' : 'contained'}
								color="warning"
								onClick={() => {
									setViewWith('10%')
								}}
								sx={{
									minWidth: 0,
									p: 0.2,
									width: 36,
									height: 32,
									fontSize: 24,
								}}
							>
								<AiOutlineMobile />
							</Button>
						</Stack>
						<Box
							component="iframe"
							srcDoc={`<!DOCTYPE HTML>
							<html lang="ja">
								<head>
									${head}
									<style>
									${style}
									.UI-editable>p {
										padding:10px;
										background : #eee;
										border: 2px dotted #ccc;
										text-align:center;
										font-size: 2rem;
									}
									</style>
								</head>
								<body>
									${body.replace(
										/{block:(\d|\w){13}}/gm,
										`<p style="width:100%;
									padding:10px;
									background : #eee;
									border: 2px dotted #ccc;
									text-align:center;
									font-size: 2rem;">編集箇所</p>`
									)}
									<script>
										${script}
										document.querySelectorAll('a').forEach((elm) => {
											elm.onclick = () => {
												return false
											}
										})
									</script>
								</body>
							</html>`}
							width="100%"
							height="700px"
							sx={{
								border: '4px solid #3ac',
								borderRadius: '0.6rem',
								background: '#fff',
							}}
						></Box>
					</Stack>
				</Stack>
			</Stack>
		</Stack>
	)
}
