import { yupResolver } from '@hookform/resolvers/yup'
import { LoadingButton } from '@mui/lab'
import { Box, Button, InputAdornment, Stack, TextField, Typography } from '@mui/material'
import { DatePicker } from '@mui/x-date-pickers'
import { format } from 'date-fns'
import { IGroup } from 'interfaces'
import { useEffect, useRef, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { AiOutlineCloseCircle, AiOutlineSave } from 'react-icons/ai'
import { CiEdit } from 'react-icons/ci'
import { useAppDispatch } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import axiosClient from 'utils/axiosClient'
import * as yup from 'yup'

type Props = {
	apiEndpoint?: string
	fieldName?: string
	label: string
	subText?: string
	value?: string | Date
	max?: number
	min?: number
	multiline?: boolean
	rows?: number
	required?: boolean
	datePicker?: boolean
	readOnly?: boolean
	useGroupName?: boolean
	group?: IGroup
	render?: () => string | JSX.Element
}
interface FormInput {
	value: any
}
export default function EditInput({
	apiEndpoint,
	fieldName,
	max,
	min,
	multiline,
	rows,
	label,
	subText,
	value = '',
	required = false,
	datePicker,
	readOnly,
	useGroupName,
	group,
	render,
}: Props) {
	const schema = yup.object({})
	const dispatch = useAppDispatch()
	const [loading, setLoading] = useState(false)
	const [edit, setEdit] = useState(false)
	const ref = useRef<HTMLDivElement | null>(null)

	const {
		register,
		handleSubmit,
		formState: { errors },
		setValue,
		getValues,
		setError,
		reset,
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			value: datePicker
				? value
					? new Date(value)
					: null
				: useGroupName && typeof value === 'string'
				? value.replace(`[${group?.name}]`, '')
				: value,
		},
	})
	useEffect(() => {
		if (value) {
			const val = useGroupName && typeof value === 'string' ? value.replace(`[${group?.name}]`, '') : value
			setValue('value', val, {
				shouldValidate: true,
			})
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [value])

	useEffect(() => {
		if (ref.current) {
			if (edit) {
				ref.current.querySelector('input')?.focus()
				ref.current.querySelector('input')?.focus()
			} else {
				ref.current.querySelector('input')?.blur()
				ref.current.querySelector('input')?.blur()
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [edit])

	const onSubmit: SubmitHandler<FormInput> = async ({ value }) => {
		if (apiEndpoint && fieldName) {
			setLoading(true)
			try {
				const {
					data: { message = '', error },
				} = await axiosClient.put<{ error?: boolean; message?: string }>(apiEndpoint, {
					[fieldName]: useGroupName && typeof value === 'string' ? `[${group?.name}]${value}` : value,
				})
				setEdit(false)
				dispatch(
					UIAction.addGlobalMessage({
						message,
						type: error ? 'error' : 'success',
					})
				)
				reset()
			} finally {
				setLoading(false)
			}
		}
	}
	return (
		<Box component="form" noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)} my={1}>
			{edit ? (
				<Stack
					direction={{ md: 'row', xs: 'column' }}
					alignItems="center"
					sx={{
						display: edit ? 'flex' : 'none',
						maxWidth: { md: 500, xs: 300 },
					}}
					ref={ref}
				>
					{datePicker ? (
						<DatePicker
							label={label}
							inputFormat="yyyy年MM月dd日"
							value={getValues('value') || null}
							onChange={(date) => {
								if (date) {
									if (date instanceof Date && !isNaN(date.valueOf())) {
										setValue('value', date, {
											shouldValidate: true,
										})
									}
								}
							}}
							onError={(error) => {
								setError('value', {
									message: error?.toString(),
								})
							}}
							renderInput={(params) => <TextField required={required} {...params} size="small" />}
						/>
					) : (
						<TextField
							variant={multiline ? 'outlined' : 'standard'}
							required={required}
							margin="none"
							size="small"
							fullWidth
							label={label}
							multiline={multiline}
							rows={rows}
							onKeyDown={(e) => {
								if (e.key === 'Escape') {
									setEdit(false)
									setValue('value', value, {
										shouldValidate: true,
									})
								}
							}}
							InputProps={
								useGroupName
									? {
											startAdornment: <InputAdornment position="start">[{group?.name}]</InputAdornment>,
									  }
									: undefined
							}
							{...register('value')}
							error={'name' in errors}
							helperText={errors.value?.message}
						/>
					)}
					<Stack direction="row" sx={{ my: 1 }}>
						<Button
							sx={{
								mx: 0.5,
								px: 0.5,
								minWidth: 0,
							}}
							size="small"
							color="error"
							variant="outlined"
							onClick={() => {
								setEdit(false)
								setValue('value', value, {
									shouldValidate: true,
								})
							}}
						>
							<AiOutlineCloseCircle className="text-lg" />
						</Button>
						<LoadingButton
							disabled={Object.keys(errors).length > 0}
							loading={loading}
							sx={{
								mx: 0.5,
								px: 0.5,
								minWidth: 0,
							}}
							size="small"
							variant="outlined"
							type="submit"
						>
							<AiOutlineSave className="text-lg" />
						</LoadingButton>
					</Stack>
				</Stack>
			) : (
				<Stack
					direction={{ md: 'row', xs: 'column' }}
					spacing={2}
					sx={{ display: !edit ? 'flex' : 'none' }}
					alignItems={{
						md: 'center',
						xs: 'flex-start',
					}}
				>
					<Typography fontWeight="bold" variant="body2" flex={'none'}>
						{label}
					</Typography>
					<Stack direction="row" alignItems="center">
						{render ? (
							render()
						) : !datePicker && typeof value === 'string' ? (
							<Typography
								sx={{
									color: Boolean(value.trim()) ? '' : '#aaa',
								}}
							>
								{value.trim() || '未設定'}
							</Typography>
						) : (
							<Typography>
								{!value
									? '--'
									: typeof value !== 'string'
									? format(value, 'yyyy年MM月dd日')
									: format(new Date(value), 'yyyy年MM月dd日')}
							</Typography>
						)}
						<Typography mx={1}>{subText}</Typography>
						{!readOnly && (
							<Button
								sx={{
									flex: 'none',
									mx: 1,
									p: 0.2,
									minWidth: 0,
								}}
								size="small"
								variant="outlined"
								color="info"
								onClick={() => {
									setEdit(true)
								}}
							>
								<CiEdit className="text-lg" />
							</Button>
						)}
					</Stack>
				</Stack>
			)}
		</Box>
	)
}
